.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

li {
  list-style: none;
  margin: 10px auto;
  padding: 5px;
  background: cyan;
  width: 50%;
}

li:hover {
  background: azure;
  cursor: pointer;
}

input {
  background: lightblue;
  border: none;
  border-radius: 5px;
  padding: 10px;
  margin: 5px auto;
}

button {
  background: rgb(7, 98, 56);
  margin: 2px auto;
  border: none;
  color: white;
  border-radius: 5px;
  padding: 5px;
}

@media(max-width:650px) {
  li {
    width: 100%;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}